h1 {
  height: 100px;
}

body{
  margin-top: 50px;
}

article{
  margin: 10px;
}
h1 span {
  position: relative;
  top: 20px;
  display: inline-block;
  animation: bounce .3s ease infinite alternate;
  font-family: 'Titan One', cursive;
  font-size: 80px;
  color:whitesmoke;
  text-shadow: 0 1px 0 #CCC,
               0 2px 0 #CCC,
               0 3px 0 #CCC,
               0 4px 0 #CCC,
               0 5px 0 #CCC,
               0 6px 0 transparent,
               0 7px 0 transparent,
               0 8px 0 transparent,
               0 9px 0 transparent,
               0 10px 10px rgba(0, 0, 0, .4);
}

h1 span:nth-child(2) { animation-delay: .1s; }
h1 span:nth-child(3) { animation-delay: .2s; }
h1 span:nth-child(4) { animation-delay: .3s; }
h1 span:nth-child(5) { animation-delay: .4s; }
h1 span:nth-child(6) { animation-delay: .5s; }
h1 span:nth-child(7) { animation-delay: .6s; }
h1 span:nth-child(8) { animation-delay: .7s; }
h1 span:nth-child(9) { animation-delay: .8s; }
h1 span:nth-child(10) { animation-delay: .9s; }
h1 span:nth-child(11) { animation-delay: .10s; }


@keyframes bounce {
  100% {
    top: -20px;
    text-shadow: 0 1px 0 #CCC,
                 0 2px 0 #CCC,
                 0 3px 0 #CCC,
                 0 4px 0 #CCC,
                 0 5px 0 #CCC,
                 0 6px 0 #CCC,
                 0 7px 0 #CCC,
                 0 8px 0 #CCC,
                 0 9px 0 #CCC,
                 0 50px 25px rgba(0, 0, 0, .2);
  }
}

.Logo {
  display: inline-block; /* 글자에 맞게 크기를 조절합니다. */
  font-weight: bold;
  border-radius: 10px;
  background-color: blanchedalmond;
  padding: 10px 20px; /* 상하 10px, 좌우 20px의 여백을 설정합니다. */
  line-height: 30px;
  margin: 100px;
}

.App {
  text-align: center;
  background-color:white;
  min-height: 100vh;
}

img {
  height: 50vmin;
}

.App-link1, .App-link2, .App-link3{
  text-decoration:none;
  color: black;
  font-size:20px;

}

@media (prefers-reduced-motion: no-preference) {
  .Logo {
    animation: Logo-spin infinite 20s linear;
   }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@keyframes Logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Name{
  font-size: 20px;
  font-weight: 180;
}

.Thanks{
  font-size: 30px;
  font-weight: 500;
}

.By{
  color: #CCC;
  margin-top: 100px;
  font-size: small;
}